import { Component, h, Host, Prop } from '@stencil/core';
import { TpColor } from '../../../../utils/types/color.type';
import { createColorClasses } from '../../../../utils/functions/color.function';

@Component({
  tag: 'tp-add-card',
  styleUrl: 'add-card.scss',
  scoped: true,
})
export class TpAddCard {
  /**
   * todo
   */
  @Prop({ reflect: true }) color?: TpColor;

  /**
   * todo
   */
  @Prop({ reflect: true }) titulo?: string;

  /**
   * todo
   */
  @Prop({ reflect: true }) iconName?: string;

  render() {
    const { titulo, iconName, color } = this;

    return (
      <Host
        class={createColorClasses(color, {
          'tp-add-card': true,
        })}
      >
        <ion-item
          class="tp-add-card__container"
          tp-spacingx="s16"
          tp-spacingy="s16"
        >
          <ion-icon
            slot="start"
            class="tp-add-card__icon"
            src={iconName}
          ></ion-icon>
          <ion-text class="tp-add-card__text" tp-type="p16xb">
            <span>{titulo}</span>
          </ion-text>
        </ion-item>
      </Host>
    );
  }
}
