:host {
  cursor: pointer;
  display: block;
}

.tp-add-card {
  &__container {
    --border-radius: 8px;
    --background: var(--tp-color-neutral-2);
    display: flex;
    align-items: center;
    max-width: 366px;
  }

  &__icon {
    color: var(--tp-color-neutral-10);
    width: 24px;
    height: 24px;
  }
}

//
// color
//
:host(.tp-color) {
  .tp-add-card {
    &__container {
      --background: var(--tp-color-base);
    }

    &__icon {
      color: var(--tp-color-contrast);
    }

    &__text {
      color: var(--tp-color-contrast);
    }
  }
}